import { useState } from 'react'
import Slider from "react-slick";
// Import css files
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import SaleSP from './SaleSP'
import {sanphams } from './Sale'
// import { Outlet } from 'react-router-dom';
const config = {
    infinite: true,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 1,  
  
  };
  const choices = [
      {
        id:1,
        name:"Tt c",
      },
      {
        id:2,
        name:"Samsung",
      },      {
        id:3,
        name:"Xiaomi",
      },      {
        id:4,
        name:"TechNo",
      },      {
        id:5,
        name:"Vsmart",
      },      {
        id:6,
        name:"Masstel",
      },      {
        id:7,
        name:"IPhone",
      },      {
        id:8,
        name:"Oppo",
      },      {
        id:9,
        name:"vivo",
      },      {
        id:10,
        name:"Realme",
      },      {
        id:11,
        name:"NOkia",
      },
  ]
  const prices = [
    {
      price:"Tt c",
      text : "Tt c",
    },
    {
      text:"Di 2 triu",
    },
    {
      text :"T 2 - 4 triu",
    },
    {
      text :"T 4- 7 triu",
    },
    {
      text :"T 7 - 13 triu",
    },
    {
      text :"Trn 13 triu",
    },
  ]
function Dienthoai(){
        const [ checked , setChecked ] = useState(sanphams)
        const [ checkedPrices , setCheckedPrices ] = useState([])
        // const reRender =()=>{
        //   sanphams.filter(sanpham=> )
        // }
        const newSp=  sanphams.filter(sanpham=> {
          if(checked[0]=="Tt c") return sanpham
          return checked.includes(sanpham.brand)
        })
        const handleChecked = (name, setCheck,checkArray)=>{

          setCheck(()=>{
          
            const isChecked = checkArray.includes(name)
            if(isChecked){
                    return checkArray.filter((item)=>item!==name)
            }
            else if(name==="Tt c"){
                return ["Tt c"]
            }
            else{
                const isAll= checkArray.includes("Tt c")
                    if(isAll){
                return [name]
                    }
                return [...checkArray,name]
            }
        })
        }
return ( 
    <div className="sp_container">
        <div className="nav_bar">
           <h3 >Hng sn xut </h3>
           <div  className="choices_wrapper">
            {choices.map((choice,index)=>(
                <div key={index} className="choice">
                    <input type="checkbox" 
                    checked= {checked.includes(choice.name)}
                    onChange={()=>handleChecked(choice.name,setChecked,checked)}
                    />
                    {choice.name}
                </div>
            ))}
           </div>
           <h3 >Mc gi </h3>
           <div  className="choices_wrapper">
            {prices.map((price,index)=>(
                <div key={index} className="choice">
                    <input type="checkbox" 
                    checked= {checkedPrices.includes(price.text)}
                    onChange={()=>handleChecked(price.text,setCheckedPrices,checkedPrices)}
                    />
                    {price.text}
                </div>
            ))}
           </div>

        </div>
        <div className = "sp_wrapper">
            <div className="sp_title">
                <h1>in thoi</h1>
                <div className="slider_brand">
                <Slider {...config}>
                <img src={require("./images/iphone.jpg")}/>
                <img src={require("./images/iphone.jpg")}/>
                <img src={require("./images/iphone.jpg")}/>
                <img src={require("./images/iphone.jpg")}/>
                <img src={require("./images/iphone.jpg")}/>
                <img src={require("./images/iphone.jpg")}/>
                <img src={require("./images/iphone.jpg")}/>
            </Slider>
                </div>
        
            </div>
            <div className = "spList">
              <div className="uutien">
                <span>u tin nn xem</span>
                <button>Bn chy nht</button>
                <button>Tr gp 0%</button>
                <button>Gi thp</button>
                <button>Gi cao</button>
                <button>u i online</button>
                <div className="sort">
                <button >Bn chy nht</button>
                <ul className="sort_nav">
                  <li>Tr gp 0%</li>
                  <li>Gi thp</li>
                  <li>Gi cao</li>
                  <li>u i online</li>
                </ul>
                </div>
              </div>
            
           { newSp.length>0&&<SaleSP slider={false}title=""sanphams={newSp}/>||<SaleSP slider={false}title=""sanphams={sanphams}/>}
            </div>

        </div>
    </div>
)
}
export default Dienthoai