import React from 'react'
import {useState ,useRef} from 'react'
import { Link } from 'react-router-dom'
const TrangSP = () => {
    const [checkRadio , setCheckRadio ] = useState()
    const [color,  setColor] = useState('Vng')
    const [GBs , setGBs] = useState("128GB")
    const [ price , setPrice] = useState()
    const TenSpMua =`iPhone 13 Pro Max ${GBs} `
   const BUY=(e)=>{
        console.log("Ten sp :", TenSpMua,"Mu:",color,"Price: " , price)
        if(price){
        localStorage.setItem(`${TenSpMua}${color}`,JSON.stringify({TenSpMua,color,price,soluong:1}))
        }else{
            e.stopPropagation()
            alert("Vui lng chn sn phm bng cch tick chn  :) ")
        }
    }
    return (
        <>
         <h1 className="sp_name">iPhone 13 Pro Max {GBs} </h1>
        <div className ="trangsp_wrapper">
            <div className="row_1">
            <div className="sp_img_block ">
{color=="Vng"&& <img src={require('./images/ipvang1.jpg')}/>|| <img src={require('./images/iphone13.png')}/>}
            </div>
            <div className="view_img dl-center-center">
                <div className="view_img_item dl-column">
                <img className="icon" src={require('./images/icon.png')}/>
                <p>Xem thm 8 nh</p>
                </div>
                  <div className="view_img_item dl-column">
                <img className="icon" src={require('./images/boxicon.png')}/>
                <p>Trong hp c g</p>
                </div>
            </div>
            <div className="detailSp">
                <ul className ="detailSp_item">
                    <li>6.7", Super Retina XDR, OLED, 2778 x 1284 Pixel </li>
                    <li>12.0 MP + 12.0 MP + 12.0 MP</li>
                    <li>12.0 MP</li>
                    <li>
A15 Bionic</li>
                    <li>128 GB</li>
                </ul>
                <p className="thongso">Xem thng s k thut</p>
            </div>
            <div className="bao_hanh">
                <img src={require('./images/baohanh.png')}/>
                <span>Hng chnh hng - bo hnh 12 Thng</span>
                <img src={require('./images/ship.png')}/>
                <span>Giao hng ton quc.</span>
            </div>
            </div>
            <div className="row_2">
                <div className="price_block">
                <h1 className="price">31.990.000</h1>
                <h2  style= {{display:'inline'}}className="price_before"><strike>33.990.000</strike></h2>
                </div>
                <div className ="select">
                    <div className="select_item">
                  
                    <p className="1 GB"> <input type="radio" id="1"  checked={1==checkRadio} className="type" name="type" onChange={()=>{
                        setCheckRadio(1)
                        setGBs("128GB")
                        setPrice("31990000")
}   
                        } />128GB</p>                        
                    <p>31990000</p>
                </div>
                    <div className="select_item">
                  
                    <p className="2 GB"> <input type="radio" id="2" checked={2==checkRadio}  className="type" name="type" onChange={()=>{
                        setCheckRadio(2)
                        setGBs("256GB")
                        setPrice("34490000")
                    }
                        }/>256GB</p>                        
                    <p>34490000</p>
                </div>
                    <div className="select_item">
                  
                    <p className="3 GB"> <input type="radio" id="3" checked={3==checkRadio}  className="type" name="type"onChange={()=>{
                        setCheckRadio(3)
                        setGBs("512GB")
                        setPrice("40990000")
                        }} />512GB</p>                        
                    <p>40990000</p>
                </div>
                    <div className="select_item">
                  
                    <p className="4 GB"> <input type="radio" id="4" checked={4==checkRadio}  className="type" name="type"onChange={()=>{
                        setCheckRadio(4)
                        setGBs("1TB")
                        setPrice("46990000")
                    }
                        } />1TB</p>                        
                    <p>46990000</p>
                </div>
                </div>
                <div className="image_choose">
                        <div onClick={()=>setColor("Vng")} value="Vng" className={`choose_block ${"Vng"==color&&"active"}`}>
                        <img src={require('./images/ipvang.jpg')}/>
                            <p>Vng</p>
                        </div>
                        <div  onClick={()=>setColor("Xanh")} value ="Xanh"className={`choose_block ${"Xanh"==color&&"active"}`}>
                            <img  src={require('./images/ipxanh-1.jpg')}/>
                        <p>Xanh</p>
                        </div>
                </div>
                <div className="uudai_wrapper">
                    <div className="title">Chn 1 trong 2 khuyn mi sau</div>
                    <div className="uudai">
                        <p>L x ngay 2.000.000 p dng n 20/01 </p>
                    </div>
                    <div className="uudai">
                        <p>Tr gp 0% </p>
                    </div>
                    <div className="uudai_bonus">u i thm</div>
                    <ul className="uudai_bonus_item">
                            <li><img src={require("./images/tick_icon.png")} alt="" /><p>Tng gi iCloud 50GB min ph 3 thng</p> <span className="thongso">  Xem chi tit</span></li>
                            <li><img src={require("./images/tick_icon.png")} alt="" /><p>Gim sc 50% cho gi bo hnh vng ch t 550.000</p><span className="thongso">  Xem chi tit</span></li>
                            <li><img src={require("./images/tick_icon.png")} alt="" /><p>Thu c i mi tr gi 15%</p><span className="thongso">  Xem chi tit</span></li>
                            <li><img src={require("./images/tick_icon.png")} alt="" /><p>Bo hnh 2 nm chnh hng</p><span className="thongso">  Xem chi tit</span></li>
                            <li><img src={require("./images/tick_icon.png")} alt="" /><p>Tng PMH 200.000 mua Sc Magsafe</p><span className="thongso">  Xem chi tit</span></li>
                            <li><img src={require("./images/tick_icon.png")} alt="" /><p>Tng PMH 300.000 mua p lng chnh hng</p><span className="thongso">  Xem chi tit</span></li>
                    </ul>
                </div>
                <div className="buyBtn_wrapper">
                 <Link to="/cart">
                 <button onClick={BUY} className="buyNow">
                        <div>
                            <strong>MUA NGAY</strong>
                            <p>Giao hng min ph hoc t ti shop</p>
                        </div>
                    </button>
                     </Link>
                    <div className="tragop">
                    <button className="tragop_btn">
                        <div>
                            <strong>MUA NGAY</strong>
                            <p>Giao hng min ph hoc t ti shop</p>
                        </div>
                    </button>
                    <button className="tragop_btn">
                        <div>
                            <strong>MUA NGAY</strong>
                            <p>Giao hng min ph hoc t ti shop</p>
                        </div>
                    </button>
                    </div>
                </div>
            </div>
        </div>
        </>
           
    )
}
export default TrangSP
